@import "~foundation-sites/scss/util/util";

/** */
$unique_id: unique_id();

/** */
$black: #000000;
$white: #ffffff;

$header-color: #231F20;
$anchor-color: #4E5156;

$light-gray: #e6e6e6;
$medium-gray: #cacaca;
$gray: #9a9a9a;
$dark-gray: #8a8a8a;

$color1: #42a934;
$color2: #2e425d;
$color3: #dd2027;

$color-red: #dd2027;

$body-background: #ffffff;
$body-font-color: #000000;

/** */
$scrollbar-with: rem-calc(4);
$scrollbar-height: rem-calc(4);

/** */
$global-screen-extra: rem-calc(1360);
$global-screen: rem-calc(1200);
$global-screen-medium: rem-calc(1140);
$global-screen-small: rem-calc(960);

/** */
$mobile-width: rem-calc(640);
$desktop-width: rem-calc(1024);
$xdesktop-width: rem-calc(1200);
$xxdesktop-width: rem-calc(1440);
$xxxdesktop-width: rem-calc(1620);

$wp-mobile-width: rem-calc(782);
$wp-mobile-width-small: rem-calc(600);

/** */
$mq-breakpoints: (
        wp-mobile-small: $wp-mobile-width-small,
        mobile: $mobile-width,
        wp-mobile: $wp-mobile-width,
        desktop: $desktop-width,
        xdesktop: $xdesktop-width,
        xxdesktop: $xxdesktop-width,
        xxxdesktop: $xxxdesktop-width,
);

/** */
$header-styles-small: (
        h1: 39,
        h2: 33,
        h3: 26,
        h4: 22,
        h5: 19,
        h6: 17,
);
$header-styles-medium: (
        h1: 48,
        h2: 38,
        h3: 30,
        h4: 25,
        h5: 21,
        h6: 18,
);

/** */

$body-font-size: rem-calc(16);

$large-font-size: rem-calc(19);
$medium-font-size: rem-calc(17);
$small-font-size: rem-calc(15);

$global-line-height: 1.5;
$header-line-height: 1.4;
$paragraph-line-height: 1.6;

$gutter: 15;
$gutter-mobile: 10;

$gap: $gutter * 2;
$gap-mobile: $gutter-mobile * 2;

/** */
$header-font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI", "Ubuntu", Arial, cursive, sans-serif;
$body-font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI", "Ubuntu", Arial, cursive, sans-serif;
$font-awesome: "Font Awesome 6 Pro", "Font Awesome 6 Brands", "Font Awesome 6 Free", sans-serif;
